/* Тильда означает, что webpack ищует normalize.css в node_module. */

body {
  font-family: 'Roboto', sans-serif;
}

.container {
  padding-top: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  padding-bottom: 2rem;
}

h1 {
  text-align: center;
}

.logo {
  /*
  Если в weebpack.config установлен file-loader для изображений,
  ссылка на изображение скопируется в dist и подключется отдельно
  */
  background-image: url(09a1a1112c577c2794359715edfcb5ac.png);
  background-size: cover;
  height: 220px;
  width: 200px;
  margin: 0 auto;
}

.code {
  color: crimson;
}